            "We take the security of MetaMask Flask as seriously as any other MetaMask application, but just like a construction site is less safe thana finished building,there are more risks when using Flask than whenusing the main MetaMask applications.For example, Flask's UX is not aspolished, and it allows you to take greater risks than on the main applications.In addition, because of its experimental nature, its APlsare unstable, and things that worked in one release may be permanently broken in the next.<br><br>ln short, you can use Flask safely, but it's easier to use it unsafely.Participation is at your own risk and intended for developers only.",
        },
        {
          id: 4,
          title: "What assets and networks are available on MetaMask Flask?",
